  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.polygon2 {
  width: 52px;
  height: 52px;
  flex-shrink: 0;
  margin-left: 1.4rem;
}

/*  registration form styling */
.input-line {
  border: 2px solid #F5F5F5;
  width: 427px;
}
#register-right-container{
  margin-top: 4.4rem;
  position: relative;
}
#back-btn{
  position: absolute;
  left: -10px;
  top: -30px;
  border: none;
  font-size: 18px;
  padding: 3px;
  color:  #8692A6;
  cursor: pointer;
}
form {

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 1.6rem;
}

.input-field {
  width: 426px;
  height: 64px;
  position: relative;
}

input {
  width: 100%;
  height: 100%;
  transition: all ease 0.1s;
  outline: none;
  color: #494949;
  border-radius: 4px;
  box-shadow: 0px 10px 6px 4px   #00000018;
}
.input-field input[type=text],input[type='password'],input[type='email'] {
  padding: 10px;
}
input:hover {
  border: 2.3px solid #1565D8;
  box-shadow: 2px 1px 2px 1px #8fbeff;
}
#input-feild-checkbox{
  width: 426px;
  height: 50px;
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 7px;
}

#checkbox{
  width: 30px;
  height: 30px;
}
#show-text{
  position: absolute;
  right: 10px;
  top: 40px;
}

#checkbox-text{
  width: 220px;
  height: 19px;
}

#input-feild-or-text {
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
}
#or-text{
  width: 14px;
  height: 15px;
  font-family: Inter;
font-size: 12px;
font-weight: 400;
line-height: 15px;
letter-spacing: 0em;
text-align: center;

}
.line {
  border: 2px solid #F5F5F5;
  width: 172px;
  height: 3px;
  background-color: #F5F5F5;
}
#register-btn{
  border: none;
  background-color: #1565D8;
  color: #FFFFFF;
  line-height: 19.36px;
  border-radius: 4px;
  cursor: pointer;
}
#google-btn-container{
  display: flex;
  background-color: #000000 8%;
}
#google-logo{
  position: absolute;
  left: 45px;
  top: 20px;
}
#google-btn{
  width: 100%;
  height: 100%;
  line-height: 19.36px;
  border-radius: 4px;
  cursor: pointer;

}

